import React from 'react';
import logo from './logo.svg';
import './App.css';
import axios from "axios";
import 'bootstrap/dist/css/bootstrap.css';
import {Button} from 'react-bootstrap';

class CourseView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      courses_fetched: [],
      fetch_staus: true,
      page:0
    }
  }
  componentDidMount() {
    axios.get("course_info/",{
      auth: {
        username: 'admin11196',
        password: 'adminhassanqrsms',
      },
      params : {
        page:1
      }
    })
      .then(
        (response) => {
          this.setState(oldState => ({
            courses_fetched: response.data.results,
            fetch_staus: true,
            page:1
          }));
          console.log(response.data);
                   return;
        }
      );
  }

  next_course_page = () => {

    console.log("Current Page : " + this.state.page);
    if(this.state.page == 5){
      return;
    }



    axios.get("course_info/",{
      auth: {
        username: 'admin11196',
        password: 'adminhassanqrsms',
      },
      params : {
        page:this.state.page + 1
      }
    })
      .then(
        (response) => {
          this.setState(oldState => ({
            courses_fetched: response.data.results,
            fetch_staus: true,
            page:oldState.page + 1
          }));
          
        }
      );
  }

  render() {
   
    return (
      
      <div className="container">
        <h1> Courses </h1>
        <div >
        <Button variant="secondary" onClick={() => this.next_course_page()}>Next Course Page</Button>
          <div>
            {
              this.state.courses_fetched ? this.state.courses_fetched.map((c) => {
                return (
                  this.course_box(c)
                );
              }) : <h2> Cant Fetch Courses </h2>
            }
          </div>
        </div>
          
      </div>
    );
  }

  course_box = (course_obj) => {
    return (
      <li key={ "list_key" + course_obj.course_code}>
        <div className="card" >
          <div className="card-body">
              <h5 className="card-title">
                {course_obj.course_name}
              </h5>
              <h6>
                {course_obj.course_short}
              </h6>
              <p>
                {course_obj.course_code}
              </p>
          </div>
        </div>
      </li>
    );
  }

}

function App() {
  return (
    <div className="App">
      <CourseView />
    </div>
    

  );
}

export default App;
